.mapas{
    display: flex;
}
.seccion-mapa{
    margin-right: 1rem;
    background-color: black;
    border-radius: 5px;
    color: white;
    border: 10px white;
    -webkit-box-shadow: 4px 6px 12px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 4px 6px 12px -4px rgba(0,0,0,0.75);
    box-shadow: 4px 6px 12px -4px rgba(0,0,0,0.75);
}
.ciudad{
    display: flex;
    justify-content: center;
    font-weight: bold;
}

.acciones{
    display: flex;
    color: white;
    width: 100%;
    justify-content: space-evenly;
    margin-bottom: 2rem;
}
.accion-formulario{
    display: flex;
    flex-direction: column;
    align-items: center;
    .boton{
        background-color: #26e200;
        border-radius: 5px;
        color: #000;
        font-size: 1.5rem;
        font-weight: 900;
        padding: 5px 2rem;
        text-align: center;
        text-decoration: none;
        -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
        box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
        &:hover{
            background-color: #1ca900;
            cursor: pointer;
            color: white;
        }
    }
}
.accion-enlace{    
    display: flex;
    flex-direction: column;
    align-items: center;
    .enlace{
        background-color: #00ade2;
        border-radius: 5px;
        color: #000;
        font-size: 1.5rem;
        font-weight: 900;
        padding: 10px 10px;
        text-align: center;
        text-decoration: none;
        width: 80px;
        -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
        box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
        border-color: black;
        border-width: 5px;
        &:hover{
            background-color: #007da2;
            cursor: pointer;
            color: white;
        }
    }
}

@media(max-width: 800px) {

    .mapas {
        display: flex;
        flex-direction: column;
    }
    .seccion-mapa{
        margin-bottom: 2rem;
    }

}