header {
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
header .titulo {
  font-size: 5rem;
  color: white;
  margin: 2rem;
}

footer {
  background-color: black;
  color: white;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  display: block;
}
body .contenedor {
  background-color: rgb(73, 73, 73);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mapas {
  display: flex;
}

.seccion-mapa {
  margin-right: 1rem;
  background-color: black;
  border-radius: 5px;
  color: white;
  border: 10px white;
  -webkit-box-shadow: 4px 6px 12px -4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 6px 12px -4px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 6px 12px -4px rgba(0, 0, 0, 0.75);
}

.ciudad {
  display: flex;
  justify-content: center;
  font-weight: bold;
}

.acciones {
  display: flex;
  color: white;
  width: 100%;
  justify-content: space-evenly;
  margin-bottom: 2rem;
}

.accion-formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.accion-formulario .boton {
  background-color: #26e200;
  border-radius: 5px;
  color: #000;
  font-size: 1.5rem;
  font-weight: 900;
  padding: 5px 2rem;
  text-align: center;
  text-decoration: none;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}
.accion-formulario .boton:hover {
  background-color: #1ca900;
  cursor: pointer;
  color: white;
}

.accion-enlace {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.accion-enlace .enlace {
  background-color: #00ade2;
  border-radius: 5px;
  color: #000;
  font-size: 1.5rem;
  font-weight: 900;
  padding: 10px 10px;
  text-align: center;
  text-decoration: none;
  width: 80px;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  border-color: black;
  border-width: 5px;
}
.accion-enlace .enlace:hover {
  background-color: #007da2;
  cursor: pointer;
  color: white;
}

@media (max-width: 800px) {
  .mapas {
    display: flex;
    flex-direction: column;
  }
  .seccion-mapa {
    margin-bottom: 2rem;
  }
}
.titulo {
  color: white;
}

* {
  box-sizing: border-box;
  font-family: "Avenir", "Helvetica", sans-serif;
}

thead {
  font-size: 25px;
}

table {
  border-collapse: collapse;
  text-align: left;
  width: 100%;
}

table tr {
  border-bottom: 1px solid;
}

table th, table td {
  padding: 10px 20px;
}

table td span {
  color: rgb(0, 0, 0);
  display: none;
  font-size: 10px;
  font-weight: bold;
  padding: 5px;
  position: absolute;
  text-transform: uppercase;
  top: 0;
  left: 0;
}

@media (max-width: 800px) {
  table thead {
    left: -9999px;
    position: absolute;
    visibility: hidden;
  }
  table tr {
    border-bottom: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 40px;
  }
  table td {
    border: 1px solid;
    margin: 0 -1px -1px 0;
    padding-top: 35px;
    position: relative;
    width: 50%;
  }
  table td span {
    display: block;
  }
}